<script>
export default {
  name: 'App',
  data(){
    return{
      question:[
        {
          id:1,
          question:"Which is best for web devloper?",
          a:"Python",
          b:"Java",
          c:"JavaScript",
          d:"Android",
          ans:"JavaScript",
          res:"",
        },
        {
          id:2,
          question:"Which is best framework in javascript?",
          a:"Vue JS",
          b:"React Js",
          c:"React",
          d:"Three JS",
          ans:"Vue JS",
          res:"",
        }
      ],
      ques:-1,
      marks:0,
      finish:false,
    }
  },
  methods:{
    getResult(){
      this.question.map(a=>{
        if(a.res==a.ans){
          this.marks=this.marks+10;
        }
      })
      this.finish=true;
    }
  }
}
</script>

